/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Flyte Tyme","date":"16 Mar 2002","slug":"/2002-03-16-flyte-tyme/","tags":"Y2002"},"html":"<p>Our travel needs are pretty simple. All we want to do is to fly to Las Vegas and back.</p>\n<p>Actually, Mama has a business meeting is in Vegas, and we'll be staying in Phoenix for R&amp;R afterwards, so we'd like to fly back from Phoenix. And since Mama's employer is paying for her ticket, she needs to book separately. And she has 100,000 Lufthansa frequent flyer miles, and we'd like to pay for my ticket with her miles. That's why we're booking so late... we had to wait for a PIN from Lufthansa. And that's also why we can't take BA through London, which would be cheaper if we had to pay for all the tickets ourselves (but would mean a ground transfer from Heathrow to Gatwick). There aren't enough miles to pay for Christopher, though, so we have to book him separately, as a child under 12, but not unaccompanied.</p>\n<p>Lufthansa doesn't fly to Vegas, but we can fly in to Phoenix and take Southwest from there to Vegas, right? No, wait, there are no seats to Phoenix available as frequent flyer premium on the day we need to fly. Can we fly to Denver instead? No, flights from Denver to Vegas and Phoenix are too expensive. How about LAX? That works... we fly in to LAX, then America West to Vegas then on to Phoenix after Mama is done, all for &euro;78 each, then take Lufthansa from Phoenix back home. So that's three HAM-LAX/PHX-HAM, one booked at work, one booked at Lufthansa Miles and More, and one child booked alone, and three LAX-LAS-PHX booked together at America West.</p>\n<p><b>That was the plan. In reality,</b> the return flight from PHX was also closed as a premium flight. Mama had to call Lufthansa to set things right. So we fly through LAX on the way back as well.</p>"},"next":{"fields":{"title":"Christopher Crisis","date":"14 Mar 2002","slug":"/2002-03-14-christopher-crisis/"}},"previous":{"fields":{"title":"Bounce Bounce","date":"17 Mar 2002","slug":"/2002-03-17-bounce-bounce/"}}},{"node":{"fields":{"title":"Christopher Crisis","date":"14 Mar 2002","slug":"/2002-03-14-christopher-crisis/","tags":"Y2002"},"html":"<p>Strange, with Mozilla 0.9.9 I cannot flip my page... the http proxy says it cannot process my request.</p>\n<p>The Christopher crisis yesterday had to do with the volume of liquids. He wanted to drink his apple juice from a bottle rather than a plastic cup, and was extremely upset that 0.3 l juice from the cup did not fit into the 0.25 l bottle. He wanted all the juice in the bottle and none in the cup. It took me 15 minutes to come up with the idea to let the bottle overflow (into a bowl), so that that bottle was full and the cup was empty.</p>"},"next":{"fields":{"title":"First Steps","date":"13 Mar 2002","slug":"/2002-03-13-first-steps/"}},"previous":{"fields":{"title":"Flyte Tyme","date":"16 Mar 2002","slug":"/2002-03-16-flyte-tyme/"}}},{"node":{"fields":{"title":"First Steps","date":"13 Mar 2002","slug":"/2002-03-13-first-steps/","tags":"Y2002"},"html":"<p>The CeBIT starts today in not-too-distant Hannover. I'm glad not to be there. Meanwhile, Mama flies to Frankfurt for an impromptu afternoon meeting. Impromptu far-away meetings are never good.</p>\n<p>Lots of people are saying how they are downloading Mozilla 0.9.9, but since my <a href=\"http://www.debian.org\">system</a> has <a href=\"http://www.oreilly.com/catalog/debian/chapter/appc_05.html\">apt-get</a>, I don't have to. My nightly apt-get cron job will do it for me. Heh.</p>\n<p>\"The (US) INS is the Mickey Mouse Club of federal agencies, but this... is an insult to Mickey Mouse.\" Mama always suspected this (it took them 4 tries to issue her a Green Card with her name spelled correctly), but now we know it's <a href=\"http://www.washingtonpost.com/wp-dyn/articles/A16787-2002Mar12.html\">true</a>.</p>"},"next":{"fields":{"title":"Towers of Light","date":"12 Mar 2002","slug":"/2002-03-12-towers-of-light/"}},"previous":{"fields":{"title":"Christopher Crisis","date":"14 Mar 2002","slug":"/2002-03-14-christopher-crisis/"}}}],"pathPrefix":"","first":false,"last":false,"index":916,"pageCount":1101}}

/***/ })

